import useRequest from "../../../common/hooks/useRequest";
const useGetMatches = () => {
  const { data, loading, trigger } = useRequest({
    url: "list_matches",
    method: "get",
    isConfig: true,
  });

  const getMatches = () => {
    const payload = {
      filters: {
        created_by: 0,
      },
    };
    trigger({
      ...payload,
    });
  };

  return {
    loading,
    data: data?.data,
    options: data?.data,
    getMatches,
  };
};
export default useGetMatches;
